	module.exports = {"data":{"markdownRemark":{"html":"<p>An image illustration of RPG, \" <a href=\"/6338\">Treasure of a destroyed castle I</a> \" that was under construction at that time using N88-Japanese BASIC (86). The figure of the real system illustration is like this.</p>\n<p>Actually, the opening movie was also delusional, and there is a memory that was the scene of that storyboard. I was building a character deza of three people. Regrettably the two others disappeared.</p>\n<p>In the actual game, how was it expressed, what a ... ... <strong>\"●\"</strong> . Not three ●, three people in one group. More than Pac Man (without cutting) rich enough. I did not have a character deza from the beginning.</p>","frontmatter":{"cover":null,"date":"1995-11-01T10:00:43+09:00","strDate":"1995/11/1","title":"Treasure of the destroyed castle Ⅰ Image Illustration"}}},"pathContext":{"path":"/en/blog/1995/11/01-treasure-in-grave/","langKey":"en"}}

